body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root {
	width: 100%;
  height: 100%;
  overflow-x: hidden;
}



.file-input-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.file-input-container h1 {
	color: white;
	margin-bottom: 10px;
}

/* https://tympanus.net/codrops/2015/09/15/styling-customizing-file-inputs-smart-way/ */
.file-input-container input {
	position: absolute;
	z-index: -1;
	overflow: hidden;
	opacity: 0;
	width: 0.1px;
	height: 0.1px;
}

.file-input-container label {
	background-color: rgb(255,92,148);
	border-radius: 8px;
	color: white;
	padding: 20px;
	font-size: 1.5em;
}

.file-input-container label:hover {
	cursor: pointer;
}

.file-input-container input:focus + label {
	outline: 1px dotted #000;
	outline: -webkit-focus-ring-color auto 5px;
}

div.img {
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
	opacity: 1;
  	transition: opacity 2s linear;
}

img.loading {
	background: url(/bfv/static/media/loading.e5848307.gif) center center no-repeat;
}

img.error {
	background: url(data:image/png;base64,) center center no-repeat;
}

.iframe-loading {
	background: url(/bfv/static/media/loading.e5848307.gif) center center no-repeat;
	display:flex;
}

.bottom-bar{
	background: linear-gradient(black,grey);
	width: 100%;
	padding-bottom: 500px;
}
.source-button{
	display: inline-block;
	padding: 20px 50px;
	color: white;
	width: 150px !important;
	height: 75px !important;
}
.viewer-container {
	background-color: black;
	height: 100%;
	/* width: 100%; */
	box-sizing: border-box;
	color: #fff;
}

.item-container {
	width: 100%;
	height: calc(100% - 70px);

	display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
}

.item-container *{
	max-height: 100%;
	max-width: 100%;
}

.button-container button:hover {
	cursor: pointer;
}

.dropdown-menu.show  {
	background-color:#fff;
}

.dropdown-menu{
	height: 90vh;
	overflow-y: scroll; 
}

.dropdown-selector > button {
	color: white;
	background-color: #6c757d ;
}

.dropdown-selector button {
	color: black;
	background-color: white;
}

.button-container {
	height: 62px;
	width: 100vw;
	justify-content: center;
	display: inline-flex;
	position: relative;
	box-sizing: border-box;
}

.dropdown-selector > button,
.button-container > button {
	/* width: 100%; */
	background-color: #6c757d;
	border: none;
	border-radius: 5px !important;
	color: white;
	margin: 7px 12px !important;
}

@media  only screen and (max-width: 600px) {
	.item-container {
		height: 100%
	}

    .button-container {
		position: absolute !important;
		display: block !important;
		left: 0px;
		top: 33%;
		-webkit-transform: translateY(-50%);
		        transform: translateY(-50%);
		width: 3.5rem;
		z-index: 2 !important;
	}
	.dropdown-selector > button,
	.button-container > button {
		background-color: rgba(0,0,0,0);
		border: none;
		display: block;
		width: 100%;
	}
}
